export class Vec {
    constructor(x, y) {
        this.x = x;
        this.y = y;
    }
    get len() {
        return Math.sqrt(this.x * this.x + this.y * this.y);
    }
    get dir() {
        return Math.atan2(this.y, this.x);
    }
    static norm(a) {
        return Vec.polar(1, a.dir);
    }
    static scale(a, scalar) {
        return new Vec(a.x * scalar, a.y * scalar);
    }
    static add(a, b) {
        return new Vec(a.x + b.x, a.y + b.y);
    }
    static dot(a, b) {
        return a.x * b.x + a.y * b.y;
    }
    static eq(a, b) {
        return a.x == b.x && a.y == b.y;
    }
    static delta(a, b) {
        let dot = Vec.dot(a, b);
        let abs = Math.acos(dot / a.len / b.len);
        if (a.y * b.x > a.x * b.y)
            return -abs;
        return abs;
    }
    static polar(len, dir) {
        return new Vec(Math.cos(dir) * len, Math.sin(dir) * len);
    }
}
Vec.zero = new Vec(0, 0);
